@import url('https://rsms.me/inter/inter.css');

* {
    font-family: 'Inter var', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #000000;
    color: #cbcbcb;
    box-sizing: border-box;
}

a {
    transition: all 0.3s ease 0s;
    font-size: 16px;
    text-decoration: none;
}

a:hover {
    color: #ededed;
}

/* Navbar style */

@media screen and (max-width: 900px) {
    .cta {
      display: none;
    }
    a {
        font-size: 12px;
    }
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5%;
    border-bottom: 0.1px solid #202020;
}

.nav-links {
    list-style: none;
}

.nav-links li {
    display: inline-block;
}

.nav a {
    padding: 9px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}


/* home style */


.home {
    text-align: center;
    height: 100%;
    width: 100%;
}

.home-text .discraption a {
    color: #828282;
}
.home-text .discraption a:hover {
    color: #ffffff;
} 

@media screen and (max-width: 900px) {
    .home-text .title {
        margin-top: 70px;
        font-size: 15px;
        font-weight: 300;
    }
    .home-text .discraption {
        margin: 20px;
        font-size: 13px;
    }
}

@media screen and (min-width: 900px) {
    .home-text .title {
        margin-top: 70px;
        font-size: 20px;
        font-weight: 300;
    }

    .home-text .discraption {
        margin-top: 20px;
    }
    .content {
        max-width: 850px; 
        margin: 0 auto !important; 
        float: none !important; 
    }
    .BgAnimation__svg {
        margin-top: 50px;
        max-width: 500px;
    }
}
  